@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
@import url(https://fonts.googleapis.com/css?family=Lexend+Deca|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: 'Lexend Deca', sans-serif;
    font-weight: bold;
    margin: 0px 0px 0px -10px;
    padding-left: 7%;
    padding-top: 20px;
    padding-bottom: 15px;
    position: absolute;
    z-index: 100;
    width: 92.5%;
    top: 0;
}

.topNav {
    float: right;
    padding: 0% 5% 0% 0%;
    font-size: 13px;
}
  
.navBar a:hover {
    color: white;
}

.dropNav {
    float: right;
    overflow: hidden;
    display: none;
    margin-right: 2vw;
}

#dropbtn {
    background-color: transparent;
    font-size: 25px;
    /* border: 2px solid black;
    letter-spacing: .15em;
    text-transform: uppercase;
    font-weight: bold;
    padding: 7px 10px; */
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: transparent;
    min-width: 160px;
    z-index: 1;
    text-align: right;
}

.dropNav:hover .dropdown-content {
    display: block;
    margin-left: -130px;
}

.min-screen {
    float: none;
    color: black;
    font-weight: normal;
    padding: 5px 0px;
    text-decoration: none;
    display: block;
    text-align: right;
    font-size: 13px;
}


@media screen and (max-width: 800px) {
    .dropNav {
        display: block;
    }
    
    .topNav {
        display: none;
    }
  }
.footer {
    font-size: 25px;
    width: 98%;
    margin-left: 2%;
    font-style: italic;
    position: fixed;
    letter-spacing: .25em;
    background-color: white;
    bottom: 0;
    padding: 15px 0px;
    display: flex;
    justify-content: space-between;
  }

#footer-right {
  float: right;
  width: 20%;
}
  
.linkedin, .github {
  padding: 7px;
}

.email {
  padding: 0px 7% 0px 7px;
}

.credits {
  display: none;
}

@media screen and (max-width: 800px) {
  .footer {
    position: relative;
    bottom: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f79f99;
    height: 125px;
    width: 101%;
    margin-left: -1%;
    margin-top: -5%;
    padding-left: 1%;
  }
  
  .credits {
    display: block;
    font-size: 2vw;
    letter-spacing: normal;
    padding-top: 25px;
    font-style: normal;
  }

  #footer-right {
    width: 100%;
    margin-left: 5%;
  }
}

@media screen and (max-width: 450px) {
  .footer {
    height: 75px;
  }

  .credits {
    padding-top: 15px;
  }
}
#about {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    margin: 115px 10%;
    height: 100vh;
}

h2 {
    letter-spacing: .075em;
}

.skills {
    display: flex;
    flex-direction: column;
    width: 35%;
    display: block;
}

.skills img {
    display: right;
    height: 65%;
    width: auto;
}

.summary {
    width: 40%;
    text-align: left;
    line-height: 1.5em;
    text-align: justify;
}



@media screen and (max-width: 1000px) {
    #about {
        display: flex; 
        flex-direction: column;
        justify-content: space-evenly;
        margin: 15% 5%;
        align-items: center;
    }
    .skills img {
        margin: 15vh 0 5vh -19vw;
        height: 375px;
        width: 70vw;
        object-fit: cover;
    }
    .summary {
        width: 80%;
        margin-bottom: 25vh;
    }
}

@media screen and (max-width: 800px) {
    #about {
        margin-top: -25%;
    }
    .skills img {
        margin: -55vh 0 -35vh -19vw;
    }
    .summary {
        margin-top: -17%;
        margin-bottom: -95vh;
    }
}

@media screen and (max-width: 550px) {
    #about {
        margin-top: -25vh;
    }
}
#contact-me { 
    background: linear-gradient(35deg, #ffffff 65%, #f79f99 45%);
    position: absolute;
    width: 100% !important;
    overflow-x: hidden !important;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.helloText {
  margin-top: 17vh;
}

.helloText h1 {
  font-size: 5vw;
}

.helloText p {
  font-size: 1.7vw;
}

.sayHello {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1%;
    font-size: 2vw;
    margin-left: -75px;
    margin-top: 50px;
}


@media screen and (min-width: 1100px) {
  .sayHello {
     font-size: 17px;
  }
}

@media screen and (max-width: 800px) {
  #contact-me { 
    height: 75vh;
}
  
  .helloText {
    margin-top: 20vh;
  }
  
  .helloText h1 {
    font-size: 7vw;
  }
  
  .helloText p {
    font-size: 2vw;
  }

  .sayHello {
    margin-left: -10%;
 }
}


#projects {
    background: #f7f7f7;
    min-height: 100vh;
    width: 100% !important;
    overflow-x: hidden !important;
}
  
.project-title {
    font-weight: bold;
    text-align: center;
    font-size: 3.75vw;
    letter-spacing: .17em;
    padding: 12vh 0px 0px 0px;
}

.first-row-project {
  display: flex; 
  flex-direction: row;
  justify-content: space-evenly;
  margin: 2.5%;
  min-height: 55vh;
}
  
#projects img {
    width: 500px;
    height: 275px; 
    object-fit: cover;
    margin: 0px 3vw 3vh 3vw ;
}
  
.first-project-left:hover, .first-project-right:hover {
    opacity: .45;
}

@media screen and (max-width: 1150px) {
    .first-row-project {
        display: flex; 
        flex-direction: column;
        justify-content: space-evenly;
        margin: 5%;
      }
}

@media screen and (max-width: 600px) {
    #projects img {
        width: 400px;
        height: 250px; 
        object-fit: cover;
        margin: 0px 3vw 3vh 3vw ;
    }
}

@media screen and (max-width: 450px) {
    #projects img {
        width: 300px;
        height: 175px; 
        object-fit: cover;
        margin: 0px 3vw 3vh 3vw ;
    }
}



