#welcome-page {
  min-height: 100vh;
  width: 100% !important;
  overflow-x: hidden !important;
}

.welcomeHeader {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 5vw;
    text-transform: lowercase;
    font-weight: bold;
    letter-spacing: .15em;
}
  
.typeWrite {
  font-family: 'Lexend Deca';
  overflow: hidden;
  white-space: nowrap;
  letter-spacing: .15em;
  -webkit-animation: 
    typing 3.5s steps(40, end);
          animation: 
    typing 3.5s steps(40, end);
}
  
/* The typing effect */
@-webkit-keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
  
.top {
    background-color: #f79f99;
    padding: 10% 0% .5% 0%;
    justify-content: center;
    display: flex;
    height: 50%;
}
  
.bottom {
    padding: 0% 25% 0% 10%;
    justify-content: center;
    align-items: center;
    display: flex;
}
  
.exploreButton {
    position: absolute;
    border: 2px solid black;
    text-transform: uppercase;
    letter-spacing: .15em;
    font-weight: bold;
    padding: 1.5%;
    font-size: 2vw;
}




@media screen and (min-width: 1100px) {
  .exploreButton {
     font-size: 17px;
  }
}

@media screen and (max-width: 800px) {
  .bottom {
    padding: 0% 25vw 0% 7vw;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  #welcome-page {
    background-color: #f79f99;
    padding: 25vh 15vw 0vh 12vw;
    justify-content: left;
    display: flex;
    min-height: 75vh;
  }

  .typeWrite {
    font-size: 8vw;
  }

}

@media screen and (max-width: 550px) {
  #welcome-page {
    padding: 31vh 15vw 0vh 8vw;
  }
}

